import React from "react";
import styled from "styled-components";
import YouTube from "react-youtube";

import { Avatar, Box } from "@mui/material";
import { BasicCard } from "./Mobile Components/Cards/Card";

import { PhotoCardComponent } from "./1 introduction/components/photosCards/PhotoCard";
import { SocialContainer } from "./1 introduction/c1 - Left/c1c2 - Bottom/c1c2c3 - Right/c1c2c3c1to3 - Social/socialContainer";
import { SocialContainerDG } from "./1 introduction/c1 - Left/c1c2 - Bottom/c1c2c3 - Right/c1c2c3c1to3 - Social/socialContainerDG";
import { Body as DinoBody } from "./2 experiences/c2t1 - DinoGlide/c1 - bottom/c2c1 - left/c2c1c2 - body/Body";

import { Introduction } from "./1 introduction/Introduction";
import { Experiences } from "./2 experiences/experiences";
import { UNC } from "./3 education/UNC";

const MobilePageStyler = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 800vh;
  overflow: hidden;
  transform-origin: top;
  background-color: red;
  width: 100vw;
`;

const IntroStyler = styled(Box)`
  display: flex;
  flex: 1;
  max-height: 100vh;
  background-color: lightgray;
  color: white;
`;

const C1Left = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(71, 72, 97);
  max-width: 20vw;
`;

const C2Right = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 2;
  background-color: lightblue;
`;

const ExperiencesStyler = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  max-height: 100vh;
  background-image: url(https://i.ibb.co/hfq8sMn/Backdrop.png);
  justify-content: center;
`;

const C1Header = styled.h1`
  display: flex;
  flex: 1;
  flex-grow: 1;
  background-color: rgba(255, 255, 255, 0.5);
  justify-content: center;
  align-items: center;
`;

const C2Content = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 7;
  flex-direction: row;
`;

const C2C1Body = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
`;

const C2C2RightContent = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
  background-color: rgba(65, 181, 100, 0.5);
  flex-direction: column;
`;

const C2C2VideoContainer = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
  justify-content: center;
  padding-top: 5%;
  border-radius: 40px;
`;

const DinoYouTubeVideo = () => {
  const videoUrl = "M7H2HDhApis";

  const opts = {
    height: "320",
    width: "180",
    playerVars: {
      // https://developers.google.com/youtube/player_parameters
      autoplay: 0,
    },
  };

  return <YouTube videoId={videoUrl} opts={opts} />;
};

export const MobilePage = () => {
  const imgPath = require("../assets/Luke Schmidt Avatar.jpg");
  return (
    <MobilePageStyler>
      <IntroStyler>
        <C1Left>
          <Avatar
            alt="Luke Schmidt"
            src={imgPath}
            sx={{
              width: "100px",
              height: "100px",
              border: "1px solid black",
              alignSelf: "center",
            }}
          />
          <SocialContainer />
        </C1Left>
        <C2Right>
          <BasicCard
            header={"Luke Schmidt"}
            secondaryT={"Professional Portfolio"}
            secondaryU={"Computer Science BS"}
          >
            {" "}
            <br />
            <br />- Passionate Computer Science student at UNC Chapel Hill
            pursuing a BS degree with a strong foundation in coding languages
            such as Java, JavaScript, Python, and C#.
            <br />
            <br />- Skilled in developing robust applications using React/React
            Native frameworks and Unity technologies.
            <br />
            <br />- Successfully designed and launched a mobile application,
            showcasing a blend of technical proficiency and creative
            problem-solving abilities.
            <br />
            <br />- With strong experience in team management, I excel at
            coordinating project timelines to foster seamless collaboration.
            <br />
            <br />- Eager to leverage skills and knowledge to drive innovation
            in software development for a dynamic organization.{" "}
          </BasicCard>
        </C2Right>
      </IntroStyler>
      <ExperiencesStyler>
        <C1Header>Dino Glide</C1Header>
        <C2Content>
          <C2C1Body>
            <DinoBody>
              <text
                style={{
                  fontSize: ".8rem",
                }}
              >
                A captivating mobile gaming experience born from the Learn to
                Fly prototype. While initially developed as part of a team, I
                took the initiative to transform and refine the game into Dino
                Glide entirely on my own.
                <br /> <br />
                Leveraging my expertise in C# scripting for Unity, I introduced
                dynamic level generation, multiple game modes, diverse
                obstacles, customizable skins, and immersive audio elements.
                With meticulous attention to detail, I created all the assets
                from scratch. To optimize the user experience, I seamlessly
                integrated Apple Storekit and Google Play Billing for
                microtransactions, alongside Google AdMob for revenue
                generation. Through rigorous beta testing and user feedback, I
                iteratively improved gameplay and controls.
                <br /> <br />
                Dino Glide stands as a testament to my technical proficiency and
                game design acumen, making it a standout achievement in my
                repertoire. You can even give the demo to the right a try. Click
                play and control using the arrow keys.{" "}
              </text>
            </DinoBody>
          </C2C1Body>
          <C2C2RightContent>
            <C2C2VideoContainer>
              <DinoYouTubeVideo />
            </C2C2VideoContainer>
            <SocialContainerDG />
          </C2C2RightContent>
        </C2Content>
      </ExperiencesStyler>
    </MobilePageStyler>
  );
};
